import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/home",
    name: "Home",
    component: Home,
  },
  {
    path: "/",
    redirect:'/home'
  },
  {
    path: "/list",
    name: "List",
    component: () =>
      import("../views/List.vue"),
  },
  {
    path: "/cart",
    name: "Cart",
    component: () =>
      import("../views/Cart.vue"),
  },
  {
     path: "/my",
     name: "My",
     component: () =>
       import("../views/My.vue"),
   },
     {
       path: "/search",
       name: "Search",
   	children:[
   		{
   			path: "/",
   			name: "index",
   			component: () =>
   			  import("../views/search/Search-index.vue"),
   		},
   		{
   			path: "list",
   			name: "list",
   			component: () =>
   			  import("../views/search/Search-list.vue"),
   		}
   	],
       component: () =>
         import("../views/Search.vue"),
     },
	  {
	     path: "/detail",
	     name: "Detail",
	     component: () =>
	       import("../views/Detail.vue"),
	   },
	   {
	      path: "/login",
	      name: "Login",
	      component: () =>
	        import("../views/login/Login.vue"),
	    },
	   {
	      path: "/userLogin",
	      name: "UserLogin",
	      component: () =>
	        import("../views/login/UserLogin.vue"),
	    },
		{
		   path: "/address",
		   name: "Address",
		   children:[
		   	{
		   		path: "/",
		   		name: "addressIndex",
		   		component: () =>
		   		  import("../views/address/Address-Index.vue"),
		   	},
			{
				path: "address-list",
				name: "address-list",
				component: () =>
				  import("../views/address/Address-List.vue"),
			},
		   ],
		   component: () =>
		     import("../views/Address.vue"),
		 },
		 {
		    path: "/order",
		    name: "Order",
			 meta: {
			  keepAlive: true
		  },
		    component: () =>
		      import("../views/Order.vue"),
		  },
		  {
		     path: "/pay",
		     name: "Pay",
		     component: () =>
		       import("../views/Pay.vue"),
		   },
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;